* { direction: rtl; margin: 0; padding: 0; }
.pull-center {
  margin: auto; 
}

#title { font-size: 2.5em; }
.panel-heading, .panel, .panel-body { padding: 0; }

#panelForm, #panelForm > .panel-body > * { width: 100%; }
#panelForm > .panel-body { width: 37%; margin: 3% 0 0 0; padding: 0;}

/*----------------------------------------------------------------------------*/
#testTable { width: 100% }
th, td { text-align: center; width: auto; }

.nameCell { width: 25%; text-align: right;}
.numberCell { width: 5%; text-align: right; }

tbody > tr:first-child > td { border-top: solid silver; }

.borderTop { border-top: 2px solid; }
.borderCat { border: 2px solid lightgray; }
.cat { font-weight: bold; }
/*----------------------------------------------------------------------------*/
#list1 > .list-group-item, #sel {
  width: 35%;
  margin: 0;
  padding: 0 1% 0 2%;
  height: 2em;
}

.badge { vertical-align: middle; margin: 1% 0 1% 0; } 
h1,h2,h3,h4 { letter-spacing: .08em; }

.checkbox label {
    margin-right: 15px;
    margin-top: 0;
}

.list-group-item { border-radius: 0; }
.list-group-item a { margin: auto 1% auto 1%;  padding-left: 1%; border-left: 1px solid black; }
.list-group-item a:last-child { border: none;}

@media only screen and (max-width: 768px) {
  #title { font-size: 1.6em; }

  #testTable, #testTable > tbody, #testTable > tbody > tr { width: 100%; }

  #list2 {
    padding: 0;
    margin: 0;
  }
  
  #list2 > .list-group-item {
    width: 100%;
    margin: 0;
  }
  
  .panel-heading > h3 { font-size: 1.6em; } 
   #list1 > .list-group-item {
     width: 80%;
   }

   #panelForm, #panelForm > .panel-body > * { width: 100%; }
   #panelForm > .panel-body { width: 92vw; margin: 0; padding: 0;}

   .col-sm-12, .col-sm-12 > * , .col-xs-12, .col-xs-12 > * {
    width: 92vw;
    margin-left: 4vw;
  }
 
 
  .list-group-item a { height: 10px; margin: auto 2% auto 2%; padding-left: 3%; border-left: 1px solid black; }
  .list-group-item a:last-child { border: none;}
  .list-group-item, .list-group-item .active { border-radius: 0;}

}
